.About{
    color:#474747;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 68px;
    gap: 15px;
}

.About h1, .about h2{
    font-weight: normal;
}

.small-text{
    text-align: left;
    font-size: 0.96rem;
    font-weight: lighter;
}

.About ul{
    display: flex;
    flex-direction: row;
    gap: 15px;
}

.About ul a i{
    font-size: 20px;
}